<template>
<div class="create-form">
    <icon-btn class="create-form-btn" iconClass="gd-chuangjian" btnText="创建" @click.native="createChoose(true)"></icon-btn>
    <div class="create-form__content" v-show="isShow">
        <div class="cf-content__header clearfix">
            <button class="cf-content__header-close" @click="createChoose(false)">
                <span>x</span>
            </button>
        </div>
        <div class="cf-content__body">
            <h2>选择你想要创建的场景</h2>
            <div class="cf-content__body-row">
                <div v-for="item in data" :key="item.title" class="row-item" @click="createForm(item)">
                    <img :src="item.img" width="50px" />
                    <h3>{{ item.title }}</h3>
                    <p>{{ item.tips }}</p>
                    <!-- <div class="row-item__content">
              <h3>{{item.title}}</h3>
              <p>{{item.tips}}</p>
            </div> -->
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
// import { getSubject } from "@/network/subject.js";
import {
    createTable
} from "@/network/tabledata.js";
export default {
    name: "CreateFormToolTip",
    data() {
        return {
            isShow: false,
            data: [{
                    img: "/outhor.png",
                    title: "调查问卷",
                    type: "Questionnaire",
                    tips: "满意度调出、产品调查、市场调查、学生调查",
                    path: "/formFill/maincontent",
                },
                {
                    img: "/outhor.png",
                    title: "活动报名",
                    type: "Registration",
                    tips: "活动报名、资质审核、结果推送、参会提醒、全流程覆盖",
                    path: "/formFill/maincontent",
                },
                {
                    img: "/outhor.png",
                    title: "空白表单",
                    type: "",
                    tips: "信息登记、工作日报、商品订单等，从零创建",
                    path: "/formFill/maincontent",
                },
            ],
        };
    },
    methods: {
        createChoose(bool) {
            this.isShow = bool;
        },
        createForm(item) {
            // 创建表单
            createTable(item.title).then((res) => {
                // 跳转到main页面，附带上formId
                this.$router.push(item.path + "/" + res.data.formId);
            });
        },
    },
};
</script>

<style>
.create-form {
    display: flex;
    justify-content: center;
    min-width: 100%;
    min-height: 100%;
}

.create-form-btn {
    width: 100%;
    height: 100%;
    padding: 5px;
    /* background-color: var(--color-text-change);
  color: #fff; */
}

.create-form-btn:hover {
    background-color: var(--color-btn-focus);
    color: #fff;
}

.create-form-btn:active {
    background-color: var(--color-btn-pressed);
    color: #fff;
}

.create-form__content {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 10px 50px;
    z-index: 99;
    cursor: default;
    background-color: #f4f4f4;
    color: #000;
}

.cf-content__header {
    float: right;
}

.cf-content__header-close {
    outline: none;
    border: 0;
    background: transparent;
    cursor: pointer;
    font-size: 30px;
    color: #000;
}

.cf-content__body {
    margin: 50px auto 0;
    width: 940px;
}

.cf-content__body>h2 {
    font-size: 26px;
    margin-bottom: 42px;
}

.cf-content__body-row {
    display: flex;
    flex-wrap: wrap;
    line-height: 1.5;
    text-align: left;
}

.row-item {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    margin-right: 20px;
    padding: 42px 21px;
    width: calc((100% - 40px) / 3);
    background-color: #fff;
    text-align: center;
}

.row-item:nth-child(3n + 3) {
    margin-right: 0;
}

.row-item:hover {
    box-shadow: 0 5px 15px 1px rgba(0, 0, 0, 0.1);
}

.row-item>img {
    margin: 0 auto;
}

.row-item>h3 {
    margin-top: 21px;
    margin-bottom: 7px;
    font-size: 20px;
}

.row-item>p {
    color: #666;
}
</style>
