import {
	request
} from '@/network/request.js'
// 通过表单id获取表单数据
export function getTableData(id) {
	return request({
		url: '/FormController/form/' + id
	})
}
// 创建表单
export function createTable(type) {
	return request({
		url: '/FormController/form',
		method: 'post',
		data: {
			formType: type
		}
	})
}
// 保存表单
export function saveTable(form) {
	for (let i in form.templateList) {
		delete form.templateList[i].data
		delete form.templateList[i].selected
		form.templateList[i].templateContent = JSON.stringify(form.templateList[i].templateContent)
		form.templateList[i].templateImgUrl = JSON.stringify(form.templateList[i].templateImgUrl)
		form.templateList[i].templateCount = i
	}
	let formdata = {
		formId: form.formId,
		formTitle: form.formTitle,
		formName: form.Name,
		formDesc: form.formDesc,
		formImg: null,
		templateList: form.templateList
	}
	console.log(formdata)
	return request({
		url: '/FormController/updateForm',
		method: 'put',
		data: formdata
	})
}
// 提交表单填写内容
export function submitTableData(data) {
	return request({
		url: '/DataController/Data',
		method: 'post',
		data: data
	})
}
// 获取表单是否为发布并获取表单结构数据
export function getFormation(id) {
	return request({
		url: '/FormController/getOpenForm/' + id,
	})
}
//上传图片
export function uploadImg(data) {
	return request({
		url: '/FormController/uploadImg',
		method: 'post',
		data: data
	})
}
//删除上传图片
export function deleteImg(url) {
	return request({
		url: '/FormController/deleteImg',
		method: 'delete',
		params: {
			fileUrl: url
		}
	})
}
// 删除选项
export function deleteOption(formTemplateId, optionKey) {
	let data = new FormData()
	data.append("formTemplateId", formTemplateId)
	data.append("optionKey", JSON.stringify(optionKey))
	console.log(formTemplateId, optionKey)
	return request({
		url: '/FormController/deleteFormContentAndData',
		method: 'delete',
		data: data
	})
}
